// @mui material components
import Grid from "@mui/material/Grid";

// Material Kit 2 PRO React components
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";

// Images
import Mlops from "assets/images/mlops.png";

function MLOpsPage() {
  return (
    <MKBox component="section" py={{ xs: 6, sm: 1 }}>
      <MKBox bgColor="grey-200" py={5} px={{ xs: 3, lg: 0 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} xl={6} ml="auto">
            <MKTypography variant="h5" mb={1}>
              MLOps ?
            </MKTypography>
            <MKTypography variant="body2" color="text" mb={3}>
              MLOps      (Model dev)  
               (Model Ops) .      
              ,    ,         
                          
               .         
                  (cycle)  .  MLOps 
                    . MLOps  
                      
               MLOps     .
            </MKTypography>
          </Grid>
          <Grid item xs={12} xl={4} position="relative">
            <MKBox
              component="img"
              src={Mlops}
              alt="image"
              maxWidth="18.75rem"
              width="100%"
              borderRadius="lg"
              shadow="xl"
              display={{ xs: "none", lg: "block" }}
            />
          </Grid>
        </Grid>
      </MKBox>
    </MKBox>
  );
}

export default MLOpsPage;
